@media ( max-width:576px )

  .content__head
    font-size: 48px

  .info__wrapper
    flex-direction: column

  .info
    padding-top: 130px
  .info__text
    margin-right: 0
    order: 2
  .info__image
    width: 100%
    order: 1
    margin-bottom: 30px

  .content__text-colums
    column-count: auto
  .content__text-img
    padding-bottom: 55%

  .content__quote
    padding: 55px 40px
    &::before,
    &::after
      width: 35px
      height: 35px
  .content__note
    padding: 40px

  .contact__links a
    font-size: 28px

  .post__date
    font-size: 16px

  .page-numbers
    font-size: 20px
  .page-numbers.current::before
    top: 1px
